.header {
    position: relative;
    width: 100%;
    border-bottom: 0.5px solid var(--detail-color);
    display: flex;
    flex-direction: row;
    /* align-items: center; */
    justify-content: space-between;
    height: 4rem;
    position: sticky;
    top: 0;
    z-index: 50;
    background-color: var(--background);
}

.header>h2 {
    align-self: center;
    text-align: center;
    color: var(--text-color);
}


@media (max-width: 450px) {
    .header>h2 {
        width: 10rem;
    }

